@import "main-module";

.rates {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0;
    }

    &__block {
        position: relative;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        
            border-radius: 15px;
            @include below(768px) {
                border-radius: 7px;
                object-position: 60%;
            }
        }
    }

    &__content {
        min-height: 248px;
        padding: 30px;
        @include below(768px) {
            padding: 15px 15px 40px;
        }
    }

    &__content-mx {
        max-width: 730px;
        
    }

    &__title {
        font-size: 40px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.8px;
        color: #F8F8F8;
        margin-bottom: 16px;

        @include below(1200px) {
            font-size: 32px;
        }

        @include below(768px) {
            font-size: 26px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        margin-bottom: 60px;
        color: #F8F8F8;

        @include below(768px) {
            margin-bottom: 32px;
        }
    }

    &__btn {
        display: inline-block;
        padding: 12px 20px;
    }
}