@import "main-module";

.benefits {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0;
    }

    &__block {
        padding: 30px 30px 60px 30px;
        border-radius: 15px;
        background: #f8f8f8;
        
        @include below(768px) {
            padding: 15px 15px 40px;
            border-radius: 7px;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 60px;

        @include below(768px) {
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__count {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__cards {
        width: 100%;
        max-width: 842px;

        display: flex;
        flex-wrap: wrap;
        margin: -60px -12px 0;
        margin-left: auto;

        @include below(1024px) {
            margin: -60px -12px 0;
            max-width: initial;
        }

        @include below(500px) {
            margin: -24px 0 0;
        }
    }

    &__cards-item {
        width: calc(50% - 24px);
        margin: 60px 12px 0;
    
        @include below(500px) {
            width: 100%;
            margin: 24px 0 0;
        }
    }

    &__cards-icon {
        width: 24px;
        height: 24px;
        margin-bottom: 20px;
    }

    &__cards-title {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
        color: #404040;
        margin-bottom: 16px;
    }

    &__cards-text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        color: #404040;
    }
}