@import "main-module";

.stake {
    padding: 120px 0 60px;
    @include below(1024px) {
        padding: 60px 0 24px;
    }


    &__row  {
        display: flex;
        margin-bottom: 10px;

        @include above(769px) {
            &:nth-child(1) {
                justify-content: flex-end;
                .stake__item {
                    margin-right: 18.6%;
                }
    
                .stake__item-arrow {
                    width: 53.465px;
                    left: 20px;
                    height: 125px;
                }
            }
    
            &:nth-child(2) {
                .stake__item {
                    margin-left: 7%;
                }
    
                .stake__item-arrow {
                    width: 97.032px;
                    left: 30px;
                    height: 66.978px;
                }
            } 
    
            
            &:nth-child(3) {
                justify-content: flex-end;
    
                .stake__item {
                    margin-right: 10%;
                }
    
                .stake__item-arrow {
                    width: 197.746px;
                    height: 108px;
                }
            } 
    
            &:nth-child(4) {
                margin-bottom: 30px;
                .stake__item {
                    margin-left: 25%;
                }
    
                .stake__item-arrow {
                    width: 49.902px;
                    height: 75.862px;
    
                    left: auto;
                    right: -30px;
                }
            } 
    
            &:nth-child(5) {
                .stake__item-arrow {
                    width: 139.772px;
                    height: 73.146px;
    
                    left: auto;
                    right: -70px;
                }
            }
    
            &:nth-child(6) {
                justify-content: flex-end;
                margin-top: -30px;
                .stake__item {
    
                }
    
                .stake__item-arrow {
                    width: 236.372px;
                    height: 106.368px;
    
                    left: auto;
                    right: 70px;
                }
            } 
        }
    }

    &__item {
        display: inline-flex;
        align-items: center;
        background-color: #ECF7FF;
        padding: 6px 20px 6px 6px;
        border-radius: 80px;
        position: relative;

        @include below(768px) {
            width: 100%;
            display: flex;
        }

        img {
            width: 40px;
            height: 40px;
            min-width: 40px;
            margin-right: 10px;
            border-radius: 50%;

            @include below(768px) {
                width: 32px;
                height: 32px;
                min-width: 32px;
            }
        }

        span {
            font-size: 18px;
            font-weight: 500;
            line-height: 130%;
            color: #30B4FF;

            @include below(768px) {
                font-size: 16px;
            }
        }
    }

    &__item-arrow {
        position: absolute;
        top: 130%;
        left: 0;
        z-index: -1;

        @include below(768px) {
            display: none;
        }
        
        img {
            margin-right: 0;
            min-width: initial;
            width: 100%;
            height: 100%;
            object-fit: contain;
            border-radius: 0;
        }
    }

    &__title {
        font-size: 40px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.8px;
        color: #404040;
        text-align: center;

        @media (max-width: 1200px) {
            font-size: 32px;
        }

        @include below(768px) {
            font-size: 26px;
            margin-top: 24px;
        }

        span {
            color: #30B4FF;
        }
    }
}