@import "main-module";

.if-works {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0;
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 60px;
        padding: 0 30px;
        @include below(768px) {
            padding: 0 20px;
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__count {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        margin: -24px -12px 0;

        @include below(500px) {
            margin: -12px 0;
        }
    }

    &__cards-item {
        width: calc(33.33333% - 24px);
        margin: 24px 12px 0;
        @include below(1024px) {
            width: calc(50% - 24px);
        }

        @include below(500px) {
            width: 100%;
            margin: 12px 0 0;
        }

        &:nth-child(4) {
            width: calc(66.66666% - 24px);
        
            @include below(1024px) {
                width: calc(50% - 24px);
            }

            @include below(500px) {
                width: 100%; 
            }
        }

        &:not(:last-child) {
            border-radius: 15px;
            background: #f8f8f8;
            padding: 30px;

            display: flex;
            flex-direction: column;
            align-items: center;

            .if-works__cards-text {
                max-width: 462px;
                margin: 0 auto;
            }

            @include below(500px) {
                padding: 15px;
                border-radius: 7px;
            }
        }

        &:nth-last-child(1) {
            @include below(1024px) {
                width: 100%;
            }
        }
    }
    
    &__cards-num {
        font-size: 18px;
        font-weight: 700;
        line-height: 130%;
        color: #30b4ff;
        margin-bottom: 30px;

        @include below(500px) {
            margin-bottom: 15px;
        }
    }

    &__cards-title {
        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
        color: #404040;
        margin-bottom: 30px;
        text-align: center;

        @include below(500px) {
            margin-bottom: 15px;
        }
    }

    &__cards-text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        color: #404040;
        text-align: center;

        @include below(500px) {
            font-size: 14px;
        } 
    }

    &__cards-img {
        height: 100%;

        @include below(1024px) {
            max-height: 200px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 15px;
        }
    }
}