@import "main-module";

.payment {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0 60px;
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 60px;

        @include below(768px) {
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__count {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__block {
        border-radius: 15px;
        background: #f8f8f8;
        padding: 30px;

        @include below(768px) {
            padding: 15px;
            border-radius: 7px;
        }
    }

    &__cards {
        display: flex;
        margin: -24px -12px 0;

        @include below(768px) {
            display: block;
            margin: 0;
        }
    }

    &__item {
        width: calc(33.33333% - 24px);
        margin: 24px 12px 0;

        @include below(768px) {
            width: 100%;
            margin: 12px 0 0;
        }

        img {
            width: 100%;
        }
    }
}