@import "main-module";

.hero {
    margin-top: 40px;
    &__wrap {
        position: relative;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        img {
            height: 100%;
            border-radius: 15px;

            @include below(768px) {
                border-radius: 7px;
            }
        }
    }

    &__content {
        padding: 80px 0 100px;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include below(1024px) {
            padding: 50px 15px;
        }
    }

    &__logo {
        margin-bottom: 50px;

        @include below(1024px) {
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 56px;
        font-weight: 700;
        line-height: 110%;
        letter-spacing: 2.24px;
        text-align: center;
        color: #fff;

        margin-bottom: 24px;

        @include below(1024px) {
            font-size: 32px;
        }

        @include below(768px) {
            br {
                display: none;
            }
        }

        @include below(360px) {
            font-size: 28px;
        } 
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        text-align: center;
        color: #fff;
        margin-bottom: 50px;

        @include below(1024px) {
            font-size: 14px;
            margin-bottom: 32px;
        }

        @include below(768px) {
            br {
                display: none;
            }
        }
    }

    &__btn {
        .btn {
            padding: 12px 20px;
        }
    }
}