@import "main-module";

.tariffs {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0;
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 60px;

        @include below(768px) {
            margin-bottom: 24px;
        }
    }

    &__title {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__count {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__block {
        border-radius: 15px;
        background: #f8f8f8;
        padding: 30px;

        @include below(768px) {
            padding: 15px;
            border-radius: 7px;
        }
    }

    &__flex {
        display: flex;
        margin: 0 -12px ;
        @media (max-width: 991px) {
            display: block;
            margin: 0;
        }

        &:nth-of-type(2) {
            margin-bottom: 60px;
            align-items: flex-start;

            @media (max-width: 991px) {
                margin-bottom: 24px;
            }
        }
    }

    &__content {
        width: calc(50% - 24px);
        margin: 0 12px;

        @media (max-width: 991px) {
            margin: 0 0 12px;
            width: 100%;
        }
    }

    &__content-title {
        font-size: 40px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.8px;
        color: #404040;
        margin-bottom: 16px;
        span {
            display: block;
            color: #30B4FF;
        }

        @media (max-width: 1200px) {
            font-size: 32px;
        }

        @include below(768px) {
            font-size: 26px;
        }
    }
    &__info-text,
    &__content-text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
    }

    &__info {
        width: calc(50% - 24px);
        border-radius: 15px;
        border: 1px solid #E2E3E5;
        margin: 0 12px;
        padding: 30px;

        @media (max-width: 991px)  {
            width: 100%;
            border-radius: 7px;
            padding: 12px;
            margin: 0;
        }
    }

    &__info-head {
        display: flex;
        align-items: center;

        font-size: 18px;
        font-weight: 500;
        line-height: 130%;
        color: #30B4FF;
        margin-bottom: 20px;

        svg {
            width: 24px;
            height: 24px;
            margin-right: 16px;
        }
    }

    &__card-link {
        display: inline-block;
        padding: 12px 20px;
        background-color: transparent;
        
    }

    &__card-text {
        margin-top: 30px;
        font-size: 14px;
        font-weight: 400;
        line-height: 130%;
        color: #868686;

        @media (max-width: 991px) {
            margin-top: 15px;
        }
    }
 
    &__card {
        padding: 30px;
        border-radius: 15px;
        margin: 0 12px;
        width: calc(50% - 24px);

        @media (max-width: 991px) {
            width: 100%;
            margin: 0;
            border-radius: 7px;
            padding: 15px;
        }

        &:nth-child(1) {
            background-color: #fff;
            @media (max-width: 991px) {
                margin-bottom: 12px;
            }
            .tariffs__card-list {
                margin-bottom: 70px;

                @media (max-width: 991px) {
                    margin-bottom: 24px;
                }
            }
        }

        &:nth-child(2) {
            background-color: #ECF7FF;
        }
    }

    &__card-title {
        font-size: 24px;
        font-weight: 600;
        line-height: 110%; 
        letter-spacing: 0.48px;
        color: #30B4FF;
        margin-bottom: 30px;
        @media (max-width: 991px) {
            margin-bottom: 15px;
            font-size: 18px;
        }
    }

    &__card-subtitle {
        font-size: 12px;
        font-weight: 400;
        line-height: 130%;
        letter-spacing: 0.24px;
        color: #BBB;
        margin-bottom: 12px;
    }

    &__card-list {
        margin-bottom: 30px;
        @media (max-width: 991px) {
            margin-bottom: 24px;
        }

        li {
            font-size: 14px;
            font-weight: 400;
            line-height: 130%;
            letter-spacing: 0.28px;
            color: #404040;

            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }
    }

    &__card-item {
        position: relative;
        padding-left: 24px;

        
        svg {
            position: absolute;
            top: 0;
            left: 0;
            min-width: 16px;
            width: 16px;
            height: 16px;
        }
    }

    &__card-sublist {
        margin-top: 8px;
        padding-left: 24px;

        
        li {
            span {
                color: #30B4FF;
                font-weight: 600;
            }
            &:not(:last-child) {
                margin-bottom: 8px;
            }
        }
    }
}