@import "main-module";

.offer {
    padding: 60px 0;

    @include below(1024px) {
        padding: 24px 0;
    }

    &__block {
        border-radius: 15px;
        background: radial-gradient(88.87% 75.8% at 76.4% 50%, #545454 0%, #404040 100%);
        padding: 30px;

        @include below(768px) {
            padding: 15px 15px 40px;
            border-radius: 7px;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 60px;

        @include below(768px) {
            margin-bottom: 24px;
        }
    }

    &__header-title {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__count {
        font-size: 14px;
        font-weight: 500;
        line-height: 130%;
        color: #868686;
    }

    &__flex {
        display: flex;
    }

    &__img {
        width: 100%;
        max-width: 488px;
        margin-left: 20px;
        transform: translateY(-40px);

        @include below(1200px) {
            max-width: initial;
            width: 40%;
            transform: translateY(0px);
        }

        @include below(1024px) {
            display: none;
        }
    }

    &__content {
        @include below(1200px) {
            width: 60%;
        }

        @include below(1024px) {
            width: 100%;
        }
    }

    &__title {
        font-size: 40px;
        font-weight: 600;
        line-height: 110%;
        letter-spacing: 0.8px;
        margin-bottom: 16px;
        color: #fff;

        @include below(1200px) {
            font-size: 32px;
        }

        @include below(768px) {
            font-size: 26px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 130%;
        color: #fff;
        margin-bottom: 60px;

        @include below(1024px) {
            margin-bottom: 32px;
        }
    }

    &__btn {
        display: inline-block;
        padding: 12px 20px;
    }
}